import { Action } from '@ali/4ever-cangjie';
import Table from '../../mo/models';

export const TABLE_RESIZE_WIDTH = Symbol('TABLE_RESIZE_WIDTH');

export function resizeTableWidth(
  table: Table,
  delta: number,
  maxWidth?: number,
) {
  return {
    type: TABLE_RESIZE_WIDTH,
    payload: {
      node: table,
      delta,
      maxWidth,
    },
  };
}

interface TableResizeTableWidthPayload {
  node: Table;
  delta: number;
  maxWidth?: number;
}

interface TableResizeTableWidthAction {
  type: typeof TABLE_RESIZE_WIDTH;
  payload: TableResizeTableWidthPayload;
}

export function isTableResizeTableWidthAction(
  action: Action,
): action is TableResizeTableWidthAction {
  return action.type === TABLE_RESIZE_WIDTH;
}
